  height: 0px;
  border-radius: 0px;
  background-color: #eff;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
  opacity: 0;
  border: 1px solid #16a085;
  border-top: none;
}
.selectionator ul.list {
  list-style: none;
  padding: 0px;
  margin: 0px;
  font-weight: 900;
  overflow: hidden;
  overflow-y: hidden;
  height: 0px;
  -webkit-backface-visibility: hidden;
